.popup {
    display: none;
    &__btn {
        position: absolute;
        right: 0;
        top: 0;
        background-color: transparent;
        .icon {
            width: 20px;
            height: 20px;
            fill: #41b8ce; } } }
.popup-box {
    padding: 20px;
    @include vcenter;
    z-index: 1000;
    display: none;
    &.is-active {
        display: block; } }

.popup-slider {
    background-color: #fff;
    margin: 0 auto;
    max-width: 910px;
    &__img {
        width: 400px;
        height: 400px;
        float: right; }
    &__pages {
        font-size: 14px;
        color: #fff;
        position: absolute;
        bottom: 10px;
        right: 10px;
        min-width: 50px;
        min-height: 30px;
        background-color: #41b8ce;
        text-align: center;
        line-height: 30px; }
    &__slide {
        font-size: 0;
        position: relative; }
    &__col-left {
        display: inline-block;
        vertical-align: middle;
        width: 50%;
        font-size: 16px;
        padding-left: 50px; }
    &__col-right {
        display: inline-block;
        vertical-align: middle;
        width: 50%; } }
.popup-overlay {
    background-color: rgba(0,0,0,.5);
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 999; }
.slider-control_popup {
    background: #41b8ce;
    z-index: 1001;
    .icon {
        fill: #fff; } }
